/* TopButton.css */
.top-button {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 50px;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
  z-index: 1000;
}

.top-button.visible {
  opacity: 1;
  visibility: visible;
}

.top-button:hover {
  background-color: rgba(0, 0, 0, 0.9);
  transform: translateY(-5px);
}

@media (max-width: 768px) {
  .top-button {
    bottom: 20px;
    right: 20px;
    width: 40px;
    height: 40px;
  }
}