@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

body {
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    color: #333;
    /* overflow-x: hidden; */
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 30px;
}

section {
    padding: 80px 0;
}

.full-bleed-landing {
    width: 100%;
}

.full-bleed {
    width: 100%;
    position: relative;
}


.bg-gray {
    background-color: #f8f8f8;
}

header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(8px);
    border-bottom: 1px solid #ddd;
}

.header-inner {
    display: flex;
    align-items: center;
    height: 60px;
}

.logo {
    height: 30px;
}

.hamburger {
    display: none;
    font-size: 30px;
    background: none;
    border: none;
    color: #326CFF;
    cursor: pointer;
    padding: 8px 12px;
    border-radius: 6px;
}

.hamburger:hover {
    background: rgba(221, 221, 221, 0.3);
    color: #225beb;
}

.menu-dropdown {
    display: flex;
    align-items: center;
    gap: 30px;
}

.menu-modal {
    display: none;
    position: fixed;
    z-index: 100;
    inset: 0;
    background: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(4px);
}

.menu-modal.show {
    display: flex;
    justify-content: center;
    align-items: center;
}

.menu-modal-content {
    border-radius: 20px;
    padding: 30px;
    width: 90%;
    max-width: 320px;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.menu-modal-content nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
    gap: 16px;
    display: flex;
    flex-direction: column;
    text-align: center;
}

.menu-modal-content nav ul li a {
    text-decoration: none;
    color: #fff;
    font-weight: 600;
    font-size: 24px;
}

.menu-modal-content nav ul li a:hover {
    color: #eee;
}

.menu-modal-content nav ul li a:active {
    color: #fff;
}

.menu-modal-content .search-wrapper {
    width: 100%;
}

.menu-modal-content .search-wrapper input[type="text"] {
    width: 100%;
}

.menu-modal-content .button {
    padding: 10px 24px;
    border-radius: 20px;
}

.right-group {
    display: flex;
    align-items: center;
    margin-left: auto;
    gap: 30px;
}

.right-group nav ul {
    display: flex;
    gap: 30px;
    padding: 0;
    margin: 0;
    list-style: none;
}

.right-group nav ul li a {
    font-weight: 700;
    color: #333;
}

.right-group nav ul li a:hover {
    color: #666;
}

.right-group nav ul li a:active {
    color: #333;
}

.search-wrapper {
    position: relative;
    display: flex;
    align-items: center;
}

.search-wrapper input[type="text"] {
    padding: 8px 20px;
    border: none;
    border-radius: 20px;
    background: #fff;
    color: #333;
    font-size: 16px;
    min-width: 140px;
    width: 200px;
    border: 1px solid #ddd;
}

.search-wrapper input[type="text"]:focus {
    border: 1px solid #326CFF;
}

.search-wrapper input[type="text"]:focus::placeholder {
    color: transparent;
}

.search-wrapper input[type="image"] {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    padding: 0;
    background: none;
    border: none;
}

.button {
    padding: 8px 24px;
    border-radius: 20px;
    color: #fff;
    background-color: #326CFF;
    text-align: center;
}

.button:hover {
    background-color: #225beb;
}

.button:active {
    background-color: #326CFF;
}

.hero-section {
    background-image: url('../img/bg.jpg');
    background-size: cover;
    background-position: center center;
    display: flex;
    margin-top: -60px;
    align-items: center;
    position: relative;
    z-index: 0;
}

.hero-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 10px;
    text-align: center;
    margin-top: 120px;
}

.hero-content>a {
    margin-top: 40px;
}

.hero-title {
    text-align: center;
    font-size: 54px;
    font-weight: 800;
    color: #fff;
    margin: 0 auto;
}

.hero-sub {
    color: #fff;
    font-size: 22px;
    line-height: 30px;
}

.section-title {
    font-size: 34px;
    text-align: center;
}

.lecture-tabs {
    display: flex;
    justify-content: center;
    gap: 40px;
    margin: 30px 0 30px;
    border-bottom: 1px solid #ddd;
}

.lecture-tabs .tab {
    font-size: 18px;
    font-weight: 600;
    color: #333;
    text-decoration: none;
    padding: 10px 0;
    position: relative;
}

.lecture-tabs .tab:hover {
    color: #2970ff;
}

.lecture-tabs .tab.active::after {
    content: "";
    position: absolute;
    bottom: -1px;
    left: 0;
    right: 0;
    height: 2px;
    background-color: #2970ff;
}

.lecture-tabs .tab.active:hover {
    color: #333;
}

.lecture-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    flex-wrap: wrap;
    gap: 10px;
}


.filter-buttons {
    display: flex;
    gap: 10px;
}

.filter {
    background: #fff;
    border: 1px solid #ccc;
    border-radius: 20px;
    padding: 6px 14px;
    font-size: 14px;
    cursor: pointer;
}

.filter:hover {
    background: #f3f3f3;
}

.filter:active {
    border: 1px solid #999;
}

.lecture-cards {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}

.lecture-card {
    border: 1px solid #eee;
    padding: 20px;
    border-radius: 10px;
    width: 270px;
    font-size: 14px;
    cursor: pointer;
}

.lecture-card:hover {
    border: 1px solid #225beb;
}

.lecture-card:active {
    border: 1px solid #326CFF;
}

.badge {
    display: inline-block;
    background: #2970ff;
    color: #fff;
    padding: 4px 8px;
    border-radius: 4px;
    margin-bottom: 10px;
}

.lecture-meta {
    margin-bottom: 10px;
}

.lecture-card h3 {
    margin-bottom: 10px;
}

.period {
    color: #666;
    margin-top: 10px;
}

.view-all {
    text-align: center;
    margin-top: 60px;
}

.notice-wrapper {
    display: flex;
    justify-content: space-between;
    gap: 40px;
    margin-top: 30px;
}

.notice-list {
    flex: 1;
    min-width: 0;
}

.notice-list ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.notice-list li {
    border-bottom: 1px solid #cdcdcd;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    cursor: pointer;
}

.notice-list li:hover {
    background-color: #fff;
    color: #225beb;
    cursor: pointer;
}

.notice-list li:active {
    color: #326CFF;
}

.notice-list li a {
    text-decoration: none;
    max-width: 80%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: inherit;
    flex: 1;
}

.more-link {
    display: inline-block;
    margin: 30px 0 0 10px;
    color: #326CFF;
}

.more-link:hover {
    color: #225beb;
}

.more-link:active {
    color: #326CFF;
}

.info-section {
    margin-top: 30px;
    display: flex;
    gap: 40px;
    flex-wrap: wrap;
}

.info-contacts {
    max-width: 600px;
    margin: 0 auto;
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 30px;
    background: #f8f8f8;
    border-radius: 14px;
}

.contact-group p {
    color: #999;
    margin-bottom: 20px;
}

.contact-group ul {
    padding: 0;
    margin: 0;
}

.contact-group li {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 20px;
}

.phone {
    color: #2970ff;
    font-weight: 600;
}

.footer-inner {
    max-width: 1200px;
    margin: 0 auto;
    padding: 15px 0;
    display: flex;
    border-bottom: 1px solid #666;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.footer-links {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
}

.footer-links li {
    margin-right: 15px;
    position: relative;
    padding-right: 20px;
    line-height: 2;
}

.footer-links li:not(:last-child)::after {
    content: "|";
    position: absolute;
    right: 0;
    top: 40%;
    transform: translateY(-50%);
    color: #666;
}

.footer-links a {
    text-decoration: none;
    font-size: 14px;
    color: #fff;
}

.footer-links a:hover {
    color: #ddd;
}

.footer-links a:active {
    color: #fff;
}

.footer-select {
    position: relative;
    display: inline-block;
}

.footer-select select {
    background-color: #222;
    appearance: none;
    color: #fff;
    padding: 8px 10px;
    width: 160px;
    border-radius: 4px;
    border: 1px solid #666;
    font-size: 14px;
    text-align: left;
    direction: ltr;
}

.footer-select select:hover {

    border: 1px solid #999;
}

footer {
    background: #333;
    color: #fff;
    height: auto;
}

footer p {
    padding: 20px 0;
    font-size: 14px;
    line-height: 1.6;
    font-weight: 200;
}

@media (max-width: 1200px) {
    .container {
        padding: 0 30px;
    }
}

@media (max-width: 800px) {
    body {
        font-size: 15px;
    }

    .hamburger {
        display: block;
    }

    .menu-dropdown {
        display: none;
    }

    .search-wrapper input[type="text"] {
        width: 100%;
    }

    .notice-wrapper {
        flex-direction: column;
    }

    .notice-banners {
        width: 100%;
    }

    .notice-list li a {
        max-width: 100%;
        padding-right: 20px;
    }

    .lecture-card {
        position: relative;
        width: 100%;
    }

    .badge {
        display: inline-block;
    }

    .lecture-meta {
        position: absolute;
        top: 20px;
        right: 20px;
    }

    .lecture-card p {
        margin: 0;
    }

    .lecture-card p:first-of-type {
        display: inline-block;
    }

    .period {
        float: right;
        white-space: nowrap;
    }

    .footer-inner {
        flex-direction: column;
        align-items: flex-start;
    }

    .footer-select {
        margin-top: 10px;
    }

    .footer-select select {
        width: 100%;
    }
}

@media (max-width: 400px) {
    body {
        font-size: 14px;
    }
}